<template>
  <div class="shopCart">
    <ul>
      <li v-for="(cart,index) in cartLists" :key="cart.id">
        <!-- 复选框 -->
        <div class="mui-input-row mui-checkbox mui-left">
          <label></label>
          <input
            name="checkbox1"
            value="Item 1"
            type="checkbox"
            v-model="getBool[cart.id]"
            @change="changeBool(cart.id,getBool[cart.id])"
          />
        </div>
        <!--图片 -->
        <p>
          <img :src="cart.image" />
        </p>
        <!-- 右侧 -->
        <div>
          <p class="title">{{cart.name}}</p>
          <!-- 下 -->
          <div>
            <!-- 价格 -->
            <span class="price">￥{{cart.price}}</span>
            <!-- 数字输入框 -->
            <div class="mui-numbox">
              <!-- "-"按钮，点击可减小当前数值 -->
              <button
                class="mui-btn mui-numbox-btn-minus"
                type="button"
                @click="changeCount(cart.id,false)"
                :disabled="getCount[cart.id]<=1"
              >-</button>
              <input class="mui-numbox-input" type="number" v-model="getCount[cart.id]" />
              <button
                class="mui-btn mui-numbox-btn-plus"
                type="button"
                @click="changeCount(cart.id,true)"
              >+</button>
            </div>
            <!-- 删除 -->
            <span @click="changeDel(cart.id,index)">删除</span>
          </div>
        </div>
      </li>
    </ul>
    <div class="clearing">
      <p>总计不含运费</p>
      <p class="f">已勾选<b style="color:red"> {{getCountBoolAll}} </b>件，总价<b style="color:red">￥{{getPrice}}</b></p>
      <button type="button" class="mui-btn mui-btn-primary">去结算</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "ShopCart",
  data() {
    return {
      cartLists: [] //api接口的数据
    };
  },
  methods: {
    changeBool(idval, boolval) {
      //修改单个购物车商品的布尔值
      this.$store.commit("changeBool", { id: idval, bool: boolval });
    },
    changeCount(idval, sub2addval) {
      //修改单个购物车中商品的数量递增或递减
      this.$store.commit("changeCount", { id: idval, sub2add: sub2addval });
    },
    changeDel(idval, index) {
      //删除当前的商品
      //删除接口中请求的数据
      this.cartLists.splice(index, 1);
      //删除本地存储的数据
      this.$store.commit("changeDel", idval);
    }
  },
  computed: {
    cartList() {
      //本地接口的数据
      return this.$store.state.cartList;
    },
    getCount() {
      //重新获取数量
      return this.$store.getters.getCount;
    },
    getBool() {
      //重新获取bool
      return this.$store.getters.getBool;
    },
    getCountBoolAll() {
      //算出已勾选的数量
      return this.$store.getters.getCountBoolAll;
    },
    getPrice() {
      let n = 0; //总价
      this.cartLists.forEach(item => {
        if (this.getBool[item.id]) {
          //在接口数据中找价格
          let price = parseInt(item.price);
          let count = this.getCount[item.id];
          n += price * count;
        }
      })
      return n;
    }
  },
  mounted() {
    //从接口请求数据
    //把vuex中数据的id筛选出来，放到数组中
    let idArr = [];
    //循环购物车
    for (let i in this.cartList) {
      idArr.push(this.cartList[i].id);
    }
    console.log(idArr);
    let para = { ids: idArr };
    this.$axios
      .get("http://47.93.224.9/api/shopcart", { params: para })
      .then(res => {
        this.cartLists = res.data.data;
        console.log(res.data.data);
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>
<style>
.shopCart > ul {
  margin-top: 20px;
}
.shopCart img {
  width: 50px;
  height: 50px;
  margin-right: 15px;
  position: relative;
  top: 10px;
}
.shopCart > ul > li {
  overflow: hidden;
  margin: 10px;
  background: #fff;
  padding: 10px 0;
}
.shopCart > ul > li > div,
.shopCart > ul > li > p {
  float: left;
}
/* 复选框 */
.mui-input-row,
.mui-checkbox label {
  margin-left: -10px;
  height: 30px;
  margin-top: 6px;
}
li .mui-input-row {
  position: relative;
  top: 10px;
}
.title,
.price {
  font-size: 14px;
  font-weight: bold;
  color: 0.222;
  height: 25px;
  line-height: 25px;
  text-align: left;
}
.price {
  display: inline-block;
  height: 25px;
  line-height: 25px;
  color: red;
}
.shopCart .mui-numbox {
  width: 105px;
  height: 25px;
  margin: 0 10px;
}
.clearing {
  background: #fff;
  text-align: left;
  margin: 10px;
  padding: 20px;
  position: relative;
}
.clearing p,b{
  font-size: 16px;
}
.clearing button{
  position: absolute;
  right: 20px;
  top: 30px;
  width: 85px;
  height: 45px;
}
</style>